"use client";
import { useEffect, useState } from "react";
import { useRouter } from "next/navigation";
import { User, Mail, Utensils, LogOut, CalendarDays } from "lucide-react";

export default function ProfilePage() {
  const [user, setUser] = useState(null);
  const router = useRouter();

  // âœ… Fetch user from localStorage
  useEffect(() => {
    const stored = localStorage.getItem("user");
    if (stored) {
      setUser(JSON.parse(stored));
    } else {
      router.push("/login"); // Redirect if not logged in
    }
  }, [router]);

  const handleLogout = () => {
    localStorage.removeItem("token");
    localStorage.removeItem("user");
    window.dispatchEvent(new Event("userUpdated")); // update navbar instantly
    router.push("/");
  };

  if (!user) return null; // prevent flicker

  return (
    <div className="min-h-screen bg-gradient-to-b from-red-50 to-orange-50 flex justify-center items-center px-4 py-10">
      <div className="w-full max-w-lg bg-white rounded-2xl shadow-xl p-8 text-gray-800">
        {/* Profile Header */}
        <div className="flex flex-col items-center text-center">
          <div className="w-24 h-24 rounded-full bg-red-100 flex items-center justify-center text-red-600 text-4xl font-bold shadow-md mb-4">
            {user.name ? user.name[0].toUpperCase() : "U"}
          </div>
          <h1 className="text-2xl font-bold text-gray-900">{user.name}</h1>
          <p className="text-gray-500 mt-1">{user.email}</p>
          <span className="mt-2 inline-block bg-red-100 text-red-600 text-sm px-3 py-1 rounded-full capitalize">
            {user.role || "Customer"}
          </span>
        </div>

        {/* Divider */}
        <hr className="my-6 border-gray-200" />

        {/* Profile Options */}
        <div className="space-y-4">
          <button
            className="w-full flex items-center gap-3 p-4 border border-gray-200 rounded-xl hover:bg-red-50 transition"
            onClick={() => alert("Coming soon: Edit Profile")}
          >
            <User className="w-5 h-5 text-red-500" />
            <span className="font-medium text-gray-800">Edit Profile</span>
          </button>

          <button
            className="w-full flex items-center gap-3 p-4 border border-gray-200 rounded-xl hover:bg-red-50 transition"
            onClick={() => router.push("/bookings")}
          >
            <CalendarDays className="w-5 h-5 text-red-500" />
            <span className="font-medium text-gray-800">My Bookings</span>
          </button>

          <button
            className="w-full flex items-center gap-3 p-4 border border-gray-200 rounded-xl hover:bg-red-50 transition"
            onClick={() => alert("Coming soon: Favourite Restaurants")}
          >
            <Utensils className="w-5 h-5 text-red-500" />
            <span className="font-medium text-gray-800">Favourite Restaurants</span>
          </button>
        </div>

        {/* Divider */}
        <hr className="my-6 border-gray-200" />

        {/* Logout */}
        <button
          onClick={handleLogout}
          className="w-full flex items-center justify-center gap-2 bg-red-500 hover:bg-red-600 text-white font-semibold py-3 rounded-xl transition shadow-md hover:shadow-lg"
        >
          <LogOut className="w-5 h-5" />
          Logout
        </button>
      </div>
    </div>
  );
}
